import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Layout, Col, Row, Space } from 'antd';
import * as d3 from 'd3';
import ChartTooltip from '../chart-tooltip/chart-tooltip.js';

import './multy-line-chart.css';

const LineChart = (props) => {
  const { id, data, xTicksFormatter, hasLegend, colors } = props;

  const [tooltipTop, setTooltipTop] = useState('auto');
  const [tooltipLeft, setTooltipLeft] = useState('auto');
  const [tooltipVisible, setTooltipVisible] = useState(false);
  const [tooltipTitle, setTooltipTitle] = useState('');
  const [tooltipValue, setTooltipValue] = useState('');

  const [legend, setLegend] = useState([]);

  const [width, setWidth] = useState(0);

  const margin = {top: 16, right: 0, bottom: 28, left: 48},
  height = window.height < 200 ? 150 - margin.bottom - margin.top : 200 - margin.bottom - margin.top,
  bisect = d3.bisector(function(d) { return d.key }).left;

  function initChart() {
    const initalWidth = document.getElementById(`chart-${id}`).getBoundingClientRect().width-margin.left;
    let svg = d3.select(`#chart-${id}`)
      .append('svg') 
      .attr('width', initalWidth+margin.left)
      .attr('height', height+margin.bottom + margin.top)
      .append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`);
    
    svg.append("g")
      .attr("class", "x-axis")
      .attr("transform", `translate(0, ${height})`);

    svg.append("g")
      .attr("class", "y-axis")
      .attr("transform", `translate(-${margin.left/3}, 0)`);

    svg.append("g")
      .attr("class", "chart-container");
      
    svg
      .append('g')
      .attr('class', 'focus')
      .append('circle')
      .attr('stroke', '#fff')
      .attr('r', 7)
      .attr('stroke-width', 8)
      .style('opacity', 0)

    setWidth(initalWidth);
  }

  function updateChart() {
    const svg = d3.select(`#chart-${id} svg g`),
    focus = svg.select('.focus circle'),
    chartContainer = svg.select('.chart-container'),
    sumstat = d3.group(data, d => d.group);

    svg.selectAll('.event-overlay').remove()

    if(hasLegend) {
      setLegend(Array.from(data).map((group,i) => {
        return {
          color: colors[i],
          group:group[0]
        }
      }))
    }
    
    const x = d3.scaleTime()
      .domain(d3.extent(data, function(d) { return d.key; }))
      .range([ 0, width ]);
    svg.selectAll('.x-axis')
      .transition()
      .duration(1000)
      .call(d3.axisBottom(x).tickFormat(xTicksFormatter ? xTicksFormatter : d => d));

    const y = d3.scaleLinear()
      .domain([0, d3.max(data, function(d) { return +d.value; })])
      .range([ height, 0 ]);

    svg.selectAll('.y-axis')
      .transition()
      .duration(1000)
      .call(d3.axisLeft(y).tickArguments([3, "s"]));

    svg.selectAll('.y-axis path, .y-axis line, .x-axis path, .x-axis line').remove();

    svg.selectAll(".tick text").attr('class', 'chart_tick_text');

    const color = d3.scaleOrdinal()
      .range(colors);

    svg.selectAll(".tick text")
      .attr('class', 'chart_tick_text');

    const line = chartContainer.selectAll('.line')
      .data(sumstat)
      .join("path")
      .attr('class', 'line')
      .attr("fill", "none")
      .attr("stroke", function(d){ return color(d[0]) })
      .attr("stroke-width", 1.5)
      .transition()
      .duration(1000)
      .attr("d", function(d){
        return d3.line()
          .x(function(d) { return x(d.key); })
          .y(function(d) { return y(+d.value); })
          (d[1])
      })
  
    function mouseover(e) {
      setTooltipVisible(true)
      focus.style('opacity', 1)
    }

    function mousemove(e) {
      let x0 = x.invert(d3.pointer(e)[0]),
      i = bisect(data, x0, 1),
      y0, selectedData, focusColor;
      if(i < data.length-1 && data[i].key.getTime() === data[i+1].key.getTime()) {
        y0 = y.invert(d3.pointer(e)[1]);
        selectedData = Math.abs(y0 - data[i].value) < Math.abs(y0 - data[i+1].value) ? data[i] : data[i+1];
        focusColor = Math.abs(y0 - data[i].value) < Math.abs(y0 - data[i+1].value) ? colors[0] : colors[1]
      } else {
        selectedData = data[i]
        focusColor = colors[0]
      }

      const tooltipXposition = x(selectedData.key),
      tooltipYposition = y(selectedData.value);
      setTooltipTitle(selectedData.key.getDate() + '/' + (selectedData.key.getMonth()+1) + '/' + selectedData.key.getFullYear());
      setTooltipValue(`${selectedData.group} - ${selectedData.value}`);
      focus
        .attr('cx', x(selectedData.key))
        .attr('cy', y(selectedData.value))
        .attr('fill', focusColor)
      setTooltipTop(tooltipYposition + margin.top + 'px')
      setTooltipLeft(tooltipXposition + margin.left + 'px')
    }

    function mouseleave(d) {
      if(d.toElement && typeof d.toElement.className === 'string' && d.toElement.className.includes('chart-tooltip')) return
      setTooltipVisible(false)
      focus.style('opacity', 0)
    }

    svg
      .append('rect')
      .style('fill', 'none')
      .attr('class','event-overlay')
      .style('pointer-events', 'all')
      .attr('width', width)
      .attr('height', height)
      .on('mouseover', mouseover)
      .on('mousemove', mousemove)
      .on('mouseout', mouseleave)
  }

  useEffect(initChart, []);

  useEffect(updateChart, [data]);

  return (
    <div className='chart-container' id={`chart-${id}`}>
      <ChartTooltip
        top={tooltipTop}
        left={tooltipLeft}
        title={tooltipTitle}
        visible={tooltipVisible}
        value={tooltipValue}
      />
    </div>
  )
};

LineChart.propTypes = {};

LineChart.defaultProps = {};

export default LineChart;
