import React, { useContext, useEffect, useState } from 'react';
import { Col, Row, Space } from 'antd';
import Section from '../../components/section/section';
import PieChart from '../../components/pie-chart/pie-chart';
import BarChart from '../../components/bar-chart/bar-chart';
import ChartCard from '../../components/chart-card/chart-card';
import MultyLineChart from '../../components/multy-line-chart/multy-line-chart';
import { ArrowRightOutlined } from '@ant-design/icons';
import EstablishmentTypeToggle from '../../components/establishment-type-toggle/establishment-type-toggle';
import Context from '../../context/context';
import { months } from '../../constants';

import './categories.css';

const Categories = (props) => {
  const {
    filteredReviews,
    filteredEstablishments,
    selectedBusinessType,
    updateSelectedBusinessType
  } = useContext(Context);

  const [establismentDistributionData, setEstablismentDistributionData] = useState(computeEstablismentDistributionData());
  const [topTenEstablishments, setTopTenEstablishments] = useState(computeTopTenEstablishments());
  const [sentimentTimeData, setSentimentTimeData] = useState(computeSentimentTimeData());
  const [visitsOverYears, setVisitsOverYears] = useState(computeVisitsOverYears());
  const [vistorsOverSubtypes, setVistorsOverSubtypes] = useState(computeVisitorsOverSubtypes());

  function computeEstablismentDistributionData() {
    const data = filteredEstablishments.reduce((establishmentAcc, establishment)=>{
      if(!establishment.subcategory) {
        establishmentAcc['Unknown'] +=1;
        return establishmentAcc;
      }
      establishment.subcategory.split(',').forEach(subcategory => {
        if(!establishmentAcc[subcategory]) {
          establishmentAcc[subcategory] = 1;
        } else {
          establishmentAcc[subcategory] += 1;
        }
      });
      return establishmentAcc;
    }, {Unknown: 0})

    if(data.Unknown === 0) {
      delete data.Unknown
    }
    return data
  }
  
  function computeTopTenEstablishments() {
    return filteredEstablishments
    .sort((a, b) => b.rating - a.rating) // Sort establishments in descending order by rating
    .slice(0, 10);
  }

  function computeSentimentTimeData() {
    let sentimentArrays = filteredReviews.reduce((sentimentAcc, review)=>{
      const formatDate = new Date(review.date).toISOString().split('T')[0];
      review.aspectsentiment_set.forEach((aspect) => {
        if(aspect.label === 'Positive') {
          sentimentAcc.positive[formatDate] ? sentimentAcc.positive[formatDate] += 1 : sentimentAcc.positive[formatDate] = 1;
        } else {
          sentimentAcc.negative[formatDate] ? sentimentAcc.negative[formatDate] += 1 : sentimentAcc.negative[formatDate] = 1;
        }
      })
      return sentimentAcc;
    }, {positive: {}, negative: {}})
    const serimentArrayMerged = [];
    Object.keys(sentimentArrays.positive).map(date => {
      serimentArrayMerged.push({
        key: new Date(date),
        group: 'Positive',
        value: sentimentArrays.positive[date]
      })
    });

    Object.keys(sentimentArrays.negative).map(date => {
      serimentArrayMerged.push({
        key: new Date(date),
        group: 'Negaitve',
        value: sentimentArrays.negative[date]
      })
    });
    return serimentArrayMerged.sort((a, b) => a.key - b.key);
  }

  function xTicksFormatter (value) {
    return `${months[value.getMonth()]} ${value.getFullYear()}`
  }

  function computeVisitsOverYears() {
    const yearMap = {};
    filteredReviews.forEach(review => {
      const date = new Date(review.date);
      const year = date.getFullYear();
      yearMap[year] ? yearMap[year] += 1 : yearMap[year] = 1;
    });

    return Object.keys(yearMap).map(year => {
      return {
        key: year,
        value: yearMap[year]
      }
    })
  }

  function computeVisitorsOverSubtypes() {
    return filteredReviews.reduce((acc, review) => {
      acc[review.establishment_subcategory] ? acc[review.establishment_subcategory] += 1 : acc[review.establishment_subcategory] = 1;
      return acc;
    }, {})
  }

  useEffect(() => {
    selectedBusinessType === '' && updateSelectedBusinessType('Accommodation');
  }, [selectedBusinessType])

  useEffect(() => {
    setSentimentTimeData(computeSentimentTimeData());
    setVisitsOverYears(computeVisitsOverYears());
    setVistorsOverSubtypes(computeVisitorsOverSubtypes());
  }, [filteredReviews])

  useEffect(() => {
    setEstablismentDistributionData(computeEstablismentDistributionData());
    setTopTenEstablishments(computeTopTenEstablishments());
  }, [filteredEstablishments])

  return (
    <>
    <Section title={(<span>Distribution of <b>Types</b></span>)}>
      <Row gutter={[24, 24]}>
        <Col span={14}>
            <EstablishmentTypeToggle/>
            <Section title={(<span>{selectedBusinessType} <b>Types</b></span>)}>
              <PieChart
                id='categories-pie'
                height={400}
                legendRight={true}
                hasLegend={true}
                data={establismentDistributionData}
              />
            </Section>
          </Col>
          <Col span={10}>
            <ChartCard>
              <h2 className='title-small'>{selectedBusinessType}</h2>
              <h3 className='title-orange'>Top 10</h3>
              <Space style={{width: '100%'}} direction='vertical' size={8}>
                {topTenEstablishments.map((establishment, index) => (
                  <div key={establishment.url} className='top-list-item'>
                    <span className='top-list-item-rating'>{index + 1} </span>
                    <a href={establishment.url} target='_blank' className='top-list-item-link' key={index}>
                      <span>{establishment.name}</span>
                      <ArrowRightOutlined className='top-list-item-arrow' />
                    </a>
                  </div>
                ))}
              </Space>
            </ChartCard>
          </Col>
        </Row>
      </Section>
      <Row gutter={[24, 24]}>
        <Col span={16}>
          <Section title={(<span><b>positive</b> and <b>negative</b> sentiments over <b>time</b></span>)}>
            <ChartCard>
              <MultyLineChart
                id='sentiment-over-time'
                xTicksFormatter={xTicksFormatter}
                data={sentimentTimeData}
                colors={['#6FCF97', '#EB5757']}
              />
            </ChartCard>
          </Section>
          <Section title={(<span>Number of <b>visitors</b> based on <b>years</b></span>)}>
            <ChartCard>
              <BarChart
                id='visits-over-years'
                data={visitsOverYears}
                color='#6FCF97'
              />
            </ChartCard>
          </Section>
        </Col>
        <Col span={8}>
          <Section title={(<span>Number of visitors based on <b>type</b></span>)}>
            <ChartCard>
              <PieChart
                id='visitors-over-subtypes'
                hasLegend={true}
                // height={200}
                data={vistorsOverSubtypes}
              />
            </ChartCard>
          </Section>
        </Col>
      </Row>
    </>
  );
}

Categories.propTypes = {};

Categories.defaultProps = {};

export default Categories;
